import React, { Component } from 'react';
import {Card, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import Paper from 'material-ui/Paper';
import MaterialPanel from './MaterialPanel';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';
class Dashboard extends Component {

	constructor(props, context) {
		super(props, context);
        this.props.setTitle("Dashboard");
	}

  render() {
    return (
        <div>
            <FloatingActionButton backgroundColor='red' style={{ margin: 20, position: 'fixed', bottom: 0, right: 0 }}>
                <ContentAdd />
            </FloatingActionButton>
            <div style={{ width: '30%', float: 'left', }}>
            <MaterialPanel title="Profile" titleColor="#ffffff" headerColor="#70d46d" body={
                <p>Test</p>
            }/>
            </div>
            <div style={{ width: '70%', float: 'right', }}>
            <MaterialPanel title="Active Team Ups" titleColor="#ffffff" headerColor="#6ad1d3" body={
                <Table>
                    <TableHeader displaySelectAll={false} adjustForCheckbox={false}>
                        <TableRow>
                            <TableHeaderColumn>Name</TableHeaderColumn>
                            <TableHeaderColumn>Date Until Close</TableHeaderColumn>
                            <TableHeaderColumn>Category</TableHeaderColumn>
                            <TableHeaderColumn>Users</TableHeaderColumn>
                            <TableHeaderColumn>Status</TableHeaderColumn>
                        </TableRow>
                    </TableHeader>
                    <TableBody displayRowCheckbox={false}>
                        <TableRow>
                            <TableRowColumn>HackOhio</TableRowColumn>
                            <TableRowColumn>10/22/17</TableRowColumn>
                            <TableRowColumn>Hackathon</TableRowColumn>
                            <TableRowColumn>112</TableRowColumn>
                            <TableRowColumn >Open</TableRowColumn>
                        </TableRow>
                    </TableBody>
                </Table>
            }/>
            </div>
        </div>
    );
  }
}

export default Dashboard;



