import React, { Component } from 'react';
import logo from './logo.svg';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import MenuItem from 'material-ui/MenuItem';
import Drawer from 'material-ui/Drawer';

import Dashboard from './components/Dashboard';
import TeamSelection from "./components/TeamSelection"

class App extends Component {

	constructor(props, context) {
		super(props, context);
        this.setTitle = this.setTitle.bind(this);
        this.setTitle("");
        this.state = {navOpen: false};
        this.handleToggle = this.handleToggle.bind(this);
	}

    handleToggle(){
        this.setState({navOpen: !this.state.navOpen});
    }

    setTitle(title){
        if(title) {
            this.title = "TeamUp | " + title;
            this.forceUpdate();
        }else{
            this.title = "TeamUp";
            this.forceUpdate();
        }
    }

  render() {

    return (
        <MuiThemeProvider>
            <AppBar onLeftIconButtonTouchTap={this.handleToggle} title={this.title} titleStyle={{ fontWeight: 'bold', }} style={{ backgroundColor: '#5472f2', }} />
            <Drawer open={this.state.navOpen} containerStyle={{ top: '64px', }}>
                <MenuItem>Test</MenuItem>
                <MenuItem>Test2</MenuItem>
            </Drawer>
            {/*<Dashboard setTitle={this.setTitle} />*/}
            <TeamSelection setTitle={this.setTitle} />
        </MuiThemeProvider>
    );
  }
}

export default App;



