import React, { Component } from 'react';
import {Card, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import Paper from 'material-ui/Paper';
import MaterialPanel from './MaterialPanel';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import AutoComplete from 'material-ui/AutoComplete';
import Toggle from "material-ui/Toggle";
import {List, ListItem} from 'material-ui/List';
import {GridList, GridTile} from 'material-ui/GridList';
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';

const styles = {
    gridList: {
        width: 500,
        height: 450,
        overflowY: 'auto',
    },
    autoComplete: {
        marginLeft:30,
        marginTop:100,
    },
    autoCompleteText: {
        fontSize:'x-large',
    }
};

const roles = [
    'Front End',
    'Back End',
    'AI Expert',
    'Business Manager',
    'Finance Manager',
    'Back End Developer',
];

const tilesData = [
    {
        img: 'images/profile1.png',
        skills: ["C++","Java"],
        name: 'Phil Dunkin',
    },
    {
        img: 'images/profile1.png',
        skills: ["Python","Java"],
        name: 'Amanda Bee',
    },
    {
        img: 'images/profile1.png',
        skills: ["ReactJS","AngularJS"],
        name: 'Subash Duco',
    },
    {
        img: 'images/profile1.png',
        skills: ["AngularJS","Express"],
        name: 'Tony Lee',
    },
];


class TeamSelection extends Component {

	constructor(props, context) {
		super(props, context);
        this.props.setTitle("Team Selection");
	}

  render() {
    return (
        <div>
            <FloatingActionButton backgroundColor='red' style={{ margin: 20, position: 'fixed', bottom: 0, right: 0 }}>
                <ContentAdd />
            </FloatingActionButton>
            <div style={{ width: '30%', float: 'left', }}>
                <AutoComplete
                    floatingLabelText="I'm looking for..."
                    filter={AutoComplete.fuzzyFilter}
                    style={styles.autoComplete}
                    textFieldStyle={styles.autoCompleteText}
                    dataSource={roles}
                    maxSearchResults={5}
                />
                <MaterialPanel title="Suggested Team Mates" titleColor="#ffffff" headerColor="#6ad1d3" body={
                    <List>
                        {tilesData.map((tile) => (
                            <ListItem
                                leftIcon = {<img src = {tile.img} style={{width: 35,height:35,clipPath: 'circle(50% at 50% 50%)'}}/>}
                                primaryText = {tile.name}
                                secondaryText={tile.skills.map((skill) => (
                                    skill.concat(' ')
                                ))}
                                rightToggle = {<Toggle/>}
                            />
                        ))}
                    </List>
                }/>

            </div>
            <div style={{ width: '70%', float: 'right', }}>
            <MaterialPanel title="My Team" titleColor="#ffffff" headerColor="#6ad1d3" body={
                <GridList cellHeight = 'auto' cols = {2} rows = {2}>
                    {tilesData.map((tile) => (
                        <GridTile
                        >
                            <p>{tile.name}</p>
                        </GridTile>
                    ))}
                </GridList>
            }/>
            </div>
        </div>
    );
  }
}

export default TeamSelection;



